.modal {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;

	z-index: 100;

	display: none;

	

	&__overlay {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		height: 100%;

		padding-top: rem(40);
		padding-bottom: rem(40);

		background-color: rgba(136, 146, 163, .5);
	}

	&__content {

		// position: absolute;
		// top: 50%;
		// margin-top: -50%;
		// left: 50%;
		// margin-left: rem(-300);

		width: rem(600);
		height: auto;
		max-height: rem(600);
		background-color: white;
		border-radius: rem(4);
		// overflow: hidden;

		

		box-shadow: 0 6px 16px 0 var(--bluey-grey-two),
		0 2px 4px 0 var(--bluey-grey-two);

		background-color: #f7fafc;

		&_header {
			padding: rem(16) rem(20);
			background-color: white;

			.text {
				font-weight: var(--font-w-sm);
				font-size: rem(18);
				letter-spacing: -0.03px;
			}
		}

		&_wrap {
			border-top: rem(1) solid var(--border-color);
			border-bottom: rem(1) solid var(--border-color);
			padding: rem(22) rem(20);
			max-height: rem(480);
			// overflow-y: auto;

			&_section {
				.section {
					display: flex;
					flex-wrap: wrap;
					align-items: center;
					margin-bottom: rem(24);

					button {
						display: flex;
						align-items: center;
						.arrow {
							transition: all 300ms ease;
							height: rem(12);
							margin-left: rem(16);
							display: flex;
							align-items: center;
						}
					}

					button.active {
						.arrow {
							transform: scale(-1);
						}
					}

					.icon-ic-drop-down {
						font-size: rem(6);
						
						
						
					}
				}

				.section_toggle {
					display: none;
				}
				.section_toggle.active {
					display: block;
				}

				.text {
					font-size: rem(18);
					display: block;
					font-weight: var(--font-w-sm);
					letter-spacing: -0.03px;
				}
			}

			.field-set {
				.label {
					display: block;
					font-weight: var(--font-w-sm);
					font-size: rem(16);
					letter-spacing: -0.03px;
				}

				.select-column {
					width: rem(143);
				}
				
			}

			.w-50 {
				width: rem(270) !important;
			}

			.button {
				padding: 0 rem(3);
				height: rem(30);

				select {
					width: 100%;
					height: rem(30);
					padding: 0 rem(7);
					position: relative;
					z-index: 2;

					font-size: rem(14);
					color: var(--slate-two);
					font-weight: var(--font-w-sm);
				}
			}
		}

		&_foot {
			padding: rem(14) rem(20);
			display: flex;
			justify-content: flex-end;
		}
	}
}

.modal.active {
	display: block;
}

.field-phone {
	display: flex;
	align-items: center;

	.select {
		position: relative;
		width: rem(42);

		.ic-arrow {
			position: absolute;
			right: rem(3);
			top: rem(2);

			pointer-events: none;
		}

		select {
			width: 100%;
			height: 95%;
			appearance: none;

			font-size: rem(15);
			color: var(--slate-two);
			font-weight: var(--font-w-sm);
		}
	}

	&__selected {
		color: var(--slate-two);
		margin-left: rem(10);
		margin-right: rem(8);
	}

	input {
		flex: 1 1 0;

		font-size: rem(15);
		color: #3c4357;

		@include placeholder {
			color: rgba(60, 67, 87, .5);
		}
	}

	

	&:focus, &:active {
		border-color: var(--border-color);
	}
}