.account-table {

	border-bottom: 0;

	.chart-table__item {
		height: rem(220);

		&:last-child, &:nth-child(4) {
			border-bottom: rem(1) solid var(--border-color);
		}
	}

	&__title {
		color: var(--battleship-grey);
		margin-bottom: rem(16);
		display: block;
	}

	&__items {
		display: flex;
		flex-direction: column;

		li {
			line-height: rem(24);

			a {
				font-weight: var(--font-w-sm);
				color: var(--cornflower);

				&:hover {
					text-decoration: underline;
				}
			}
		}
	}

}