.form_add {
	margin: rem(18) rem(20);
	border: rem(1) solid var(--border-color);
	background-color: #f7fafc;
	border-radius: rem(5);
	overflow: hidden;

	&__header {
		background-color: white;
		padding: rem(16) rem(24);

		border-bottom: rem(1) solid var(--border-color);

		.title {
			font-size: rem(24);
			margin-bottom: rem(2);
		}

		p {
			color: var(--slate-two);
			letter-spacing: 0.16px;
		}
	}

	&__foot {

		border-top: rem(1) solid var(--border-color);

		padding: rem(28) rem(24);

		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: flex-end;
		
		background-color: white;
	}

	&__content {
		padding: rem(24);

		p {
			letter-spacing: 0.16px;
			color: var(--slate-two);
		}

		.label {
			font-size: rem(16);
			letter-spacing: -0.03px;
			margin-bottom: rem(8);
			font-weight: var(--font-w-sm);
		}

		.link {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			font-weight: var(--font-w-sm);
			color: var(--cornflower);

			svg {
				margin-left: rem(4);
			}
		}

		.field,
		.select-column,
		.field-percent,
		.field-price {
			margin-bottom: rem(8);
			// width: 100%;
			width: rem(180);
			height: rem(40);
		}

		.select-column {
			padding: 0 rem(4);
			.ic-arrow {
				top: rem(14);
			}
			select {
				width: 100%;
				height: 95%;
				padding: 0 0.4375rem;
			}
		}

		.field-percent,
		.field-price {
			span {
				color: rgba(60, 67, 87, .5);
				top: rem(11);
			}
			.field {
				max-width: 100%;
			}
		}

		.field-price {
			span {
				position: absolute;
				left: rem(9);
			}
		}

		.field.first {
			max-width: rem(384);
		}

		
		
	}
}

.field-checked {
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	input {
		margin-right: rem(10);
	}

	label {
		font-weight: var(--font-w-sm);
		color: var(--slate-two);
	}
}

.field-multi {
	cursor: pointer;

	display: flex;
	flex-wrap: wrap;
	align-items: center;

	input {
		display: none;

		&:checked ~ .check {

			&::before {
				left: rem(8);
			}
		}
	}

	.check {
		position: relative;
		margin-right: rem(8);

		width: rem(26);
		height: rem(16);

		background-color: #e3e8ee;
		border-radius: rem(8);
		border: rem(1) solid #d8dbe1;

		&::before {
			content: "";
			position: absolute;
			top: rem(-1);
			left: 0;
			width: rem(16);
			height: rem(16);
			border-radius: 50%;
			background-color: white;
			border: rem(1) solid #b8bcc3;

			transition: all 300ms ease;
		}
	}

	.text {
		color: var(--slate-two);
	}
}