// components

.field {
	border-radius: rem(5);
	height: rem(30);
	width: 100%;
	padding: 0 rem(9);
	border: rem(1) solid var(--border-color);
	background-color: white;

	font-size: rem(15);
	color: var(--slate-two);

	transition: all 300ms ease;

	@include placeholder {
		color: rgba(60, 67, 87, .5);
	}

	&:active, &:focus {
		border-color: var(--cornflower);
	}
}

.field-percent {
	position: relative;

	span {
		position: absolute;
		top: rem(6);
		right: rem(9);
		font-weight: bold;
		color: var(--slate-two);
	}

	.field {
		padding-right: rem(24);
	}
}

.block-content-b {
	border-radius: rem(2);
	border: rem(1) solid var(--border-color);
	padding: rem(20);
}

.text-table-col-2 {

	> .row {
		span {
			&:first-child {
				font-size: rem(14);
				color: var(--battleship-grey);
				margin-right: rem(3);
			}
			&:last-child {
				font-size: rem(18);
				font-weight: var(--font-w-sm);
			}
		}
	}
}

.row-buttons {
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	.group {
		display: flex;
		flex-wrap: wrap;

		.button {
			&:first-child {
				border-top-right-radius: 0;
				border-bottom-right-radius: 0;

				border-right: 0;

				&::before {
					border-top-right-radius: 0;
					border-bottom-right-radius: 0;

					width: 100%;
				}
			}
			&:last-child {
				border-top-left-radius: 0;
				border-bottom-left-radius: 0;

				&::before {
					border-top-left-radius: 0;
					border-bottom-left-radius: 0;
				}
			}
		}
	}

	> span {
		font-size: rem(14);
		line-height: rem(28);
		color: var(--slate-two);
	}
}

.button {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	border-radius: rem(5);
	height: rem(30);
	padding: 0 rem(16);
	background-image: linear-gradient(to bottom, #dedfe2, #bebfc3);
	box-shadow: 0 2px 4px 0 rgba(190, 191, 195, .4);

	transition: all 500ms ease;

	&:hover {
		box-shadow: 0 2px 4px 0 rgba(190, 191, 195, .8);
	}

	&:active, &:focus {
		box-shadow: rgba(0, 0, 0, 0) 0px 0px 0px 0px,
		rgba(58, 151, 212, .36) 0px 0px 0px 4px,
		rgba(0, 0, 0, .12) 0px 1px 1px 0px,
		rgba(60, 66, 87, .16) 0px 0px 0px 1px,
		rgba(0, 0, 0, 0) 0px 0px 0px 0px,
		rgba(0, 0, 0, 0) 0px 0px 0px 0px,
		rgba(60,66,87, .08) 0px 2px 5px 0px;

		z-index: 5;

		&::before {
			width: calc(100% - 0.1250rem) !important;
		}
	}

	svg {
		margin-right: rem(8);
	}

	svg, i {
		pointer-events: none;
	}
	
	svg, i, span {
		position: relative;
		z-index: 2;
		pointer-events: none;
	}

	span {
		font-size: rem(14);
		color: var(--slate-two);
		font-weight: var(--font-w-sm);

		i {
			margin-left: rem(8);
			font-size: rem(7);
			color: #50576b;
			vertical-align: middle;
		}
	}

	.count {
		position: relative;
		right: rem(-4);
		border-left: rem(1) solid var(--border-color);
		margin-left: rem(8);
		padding-left: rem(8);
		color: var(--cornflower);
	}

	.ic-arrow {
		position: absolute;
		top: rem(7);
		right: 0;
		z-index: 3;
	}

	select {
		cursor: pointer;
		appearance: none;
		z-index: 2;
		font-size: rem(15);
		font-weight: var(--font-w-sm);
		color: var(--slate-two);
	}

	&::before {
		content: "";
		position: absolute;
		top: rem(1);
		left: rem(1);
		width: calc(100% - 0.1250rem);
		height: calc(100% - 0.1250rem);
		background-color: white;
		border-radius: rem(3);
	}
}

.button.disable {
	cursor: default;
	pointer-events: none;
	background-image: linear-gradient(to bottom, #ffffff, #ffffff),
	linear-gradient(to bottom, #eeeff0, #dedfe1);

	span {
		color: #9da1aa;
	}
}

.other_list {
	padding: 0 rem(8);
}

.button.button-blue {
	background-color: var(--cornflower);
	background-image: none;
	box-shadow: none;

	&:hover {
		background-color: var(--cornflower-h);
	}

	&::before {
		display: none;
	}

	span {
		color: white;
	}
}

.ic-plus {
	position: relative;
	width: rem(8);
	height: rem(8);


	&::before {
		position: absolute;
		top: 50%;
		margin-top: rem(-1);
		left: 0;
		content: "";
		width: 100%;
		height: rem(2);
		background-color: white;
	}

	&::after {
		content: "";
		position: absolute;
		top: rem(3);
		left: 0;
		width: 100%;
		height: rem(2);
		background-color: white;

		transform: rotate(90deg);
	}
}

.tabs-wrap {
	position: relative;
	height: rem(30);
	padding: 0 rem(12);
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	border-radius: rem(5);

	background-image: linear-gradient(to bottom, #dedfe2, #bebfc3);
	box-shadow: 0 2px 4px 0 rgba(190, 191, 195, .4);

	transition: all 500ms ease;

	&::before {
		content: "";
		position: absolute;
		top: rem(1);
		left: rem(1);
		width: calc(100% - 0.1250rem);
		height: calc(100% - 0.1250rem);
		background-color: white;
		border-radius: rem(3);
	}

	button, ul {
		position: relative;
		z-index: 2;
	}

	ul {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		margin-bottom: rem(2);

		li {

			a {
				font-size: rem(14);
				font-weight: var(--font-w-sm);
				color: var(--battleship-grey);
				padding: 0 rem(12);

				&:hover {
					color: var(--cornflower);
				}
			}
		}

		li.active {
			a {
				color: var(--cornflower);
			}
		}
	}
}

