.message_wrap {
	margin-top: rem(18);
	margin-bottom: rem(23);

	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	padding: rem(15) rem(20);
	background-color: #f7fafc;
	border: rem(1) solid var(--border-color);
	border-radius: rem(5);

	&__left {
		display: flex;
		// flex-wrap: wrap;

		flex: 0 0 75%;
		max-width: 75%;

		padding-right: rem(25);

		@include db_media {
			flex: 0 0 65%;
			max-width: 65%;

			p {
				font-size: rem(13.5);
			}
		}
	}

	&__right {
		flex: 0 0 25%;
		max-width: 25%;

		display: flex;
		flex-wrap: wrap;
		align-items: center;

		justify-content: flex-end;

		@include db_media {
			flex: 0 0 35%;
			max-width: 35%;
		}

		.button {
			svg {
				margin-right: 0;
			}
		}

		.arrow {
			margin-left: rem(30);
		}
	}
}