.form_invoice {
	margin: 0 rem(80);

	@include db_media {
		margin: 0 rem(20);
	}

	.field {
		height: rem(40);
	}

	.row {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
	}

	.form-head {
		padding: rem(10) 0;
		border-top: rem(1) solid var(--border-color);
		border-bottom: rem(1) solid var(--border-color);
		margin-bottom: rem(16);

		span, b {
			text-transform: uppercase;
			font-size: rem(12);
			letter-spacing: 0.5px;
		}

		.col {
			padding-left: rem(10);
			padding-right: rem(10);
		}
	}

	.w-10 {
		width: 10%;
	}

	.w-15 {
		width: 15%;
	}

	.w-25 {
		width: 25%;
	}

	.w-60 {
		width: 60%;
	}

	.w-72 {
		width: 72%;
	}
}


.field-price {
	width: rem(120);
	position: relative;

	b {
		position: absolute;
		top: 50%;
		margin-top: rem(-10);
		left: rem(8);
		color: var(--slate-two);
		opacity: .5;
	}

	.field {
		text-align: right;
		padding-left: rem(32);
	}
}