.aside__nav ul li [data-item="taxes"] .ic {
  position: relative;
  width: 0.875rem;
  height: 0.875rem;
}

.aside__nav ul li [data-item="taxes"] .ic::before, .aside__nav ul li [data-item="taxes"] .ic::after {
  content: "";
  position: absolute;
  width: 0.375rem;
  height: 0.375rem;
  border-radius: 50%;
  border: solid 0.09375rem #a4acba;
}

.aside__nav ul li [data-item="taxes"] .ic::before {
  top: 0;
  left: 0;
}

.aside__nav ul li [data-item="taxes"] .ic::after {
  right: 0;
  bottom: 0;
}

.aside__nav ul li [data-item="taxes"] .ic span {
  position: absolute;
  left: 0.4375rem;
  width: 0.125rem;
  height: 0.875rem;
  transform: rotate(45deg);
  background-color: #50576b;
}

.aside__nav ul li [data-item="taxes"]:hover::before, .aside__nav ul li [data-item="taxes"]:hover::after {
  border-color: var(--cornflower);
}

.aside__nav ul li [data-item="taxes"]:hover span {
  background-color: var(--cornflower);
}

.aside__nav ul li [data-item="reports"]:hover .ic .ic_path_1 {
  fill: #ADAAFD;
}

.aside__nav ul li [data-item="reports"]:hover .ic .ic_path_2 {
  fill: var(--cornflower);
}

.aside__nav ul li [data-item="settings"]:hover .ic .ic_path_1 {
  fill: var(--cornflower);
}

.aside__nav ul li [data-item="account"]:hover .ic .ic_path_1 {
  fill: #ADAAFD;
}

.aside__nav ul li [data-item="account"]:hover .ic .ic_path_2 {
  fill: var(--cornflower);
}

.aside__nav ul li.active a {
  color: var(--cornflower);
  text-shadow: 0 0 0.65px var(--cornflower), 0 0 0.65px var(--cornflower);
}

.aside__nav ul li.active [data-item="home"] .ic .ic_path_1 {
