  color: var(--slate-two);
  opacity: .5;
}

.field-price .field {
  text-align: right;
  padding-left: 2rem;
}

.account-table {
  border-bottom: 0;
}

.account-table .chart-table__item {
  height: 13.75rem;
}

.account-table .chart-table__item:last-child, .account-table .chart-table__item:nth-child(4) {
  border-bottom: 0.0625rem solid var(--border-color);
}

.account-table__title {
  color: var(--battleship-grey);
  margin-bottom: 1rem;
  display: block;
}

.account-table__items {
  display: flex;
  flex-direction: column;
}

.account-table__items li {
  line-height: 1.5rem;
}

.account-table__items li a {
  font-weight: var(--font-w-sm);
  color: var(--cornflower);
}

.account-table__items li a:hover {
  text-decoration: underline;
}

.form_add {
  margin: 1.125rem 1.25rem;
  border: 0.0625rem solid var(--border-color);
  background-color: #f7fafc;
  border-radius: 0.3125rem;
  overflow: hidden;
}

.form_add__header {
  background-color: white;
  padding: 1rem 1.5rem;
  border-bottom: 0.0625rem solid var(--border-color);
}

.form_add__header .title {
  font-size: 1.5rem;
  margin-bottom: 0.125rem;
}

.form_add__header p {
  color: var(--slate-two);
  letter-spacing: 0.16px;
}

.form_add__foot {
  border-top: 0.0625rem solid var(--border-color);
  padding: 1.75rem 1.5rem;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  background-color: white;
}

.form_add__content {
  padding: 1.5rem;
}

.form_add__content p {
  letter-spacing: 0.16px;
  color: var(--slate-two);
}

.form_add__content .label {
  font-size: 1rem;
  letter-spacing: -0.03px;
  margin-bottom: 0.5rem;
  font-weight: var(--font-w-sm);
}

.form_add__content .link {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  font-weight: var(--font-w-sm);
  color: var(--cornflower);
}

.form_add__content .link svg {
  margin-left: 0.25rem;
}

.form_add__content .field,
.form_add__content .select-column,
.form_add__content .field-percent,
.form_add__content .field-price {
  margin-bottom: 0.5rem;
  width: 11.25rem;
  height: 2.5rem;
}

.form_add__content .select-column {
  padding: 0 0.25rem;
}

.form_add__content .select-column .ic-arrow {
  top: 0.875rem;
}

.form_add__content .select-column select {
  width: 100%;
  height: 95%;
  padding: 0 0.4375rem;
}

.form_add__content .field-percent span,
.form_add__content .field-price span {
  color: rgba(60, 67, 87, 0.5);
  top: 0.6875rem;
}

.form_add__content .field-percent .field,
.form_add__content .field-price .field {
  max-width: 100%;
}

.form_add__content .field-price span {
  position: absolute;
  left: 0.5625rem;
}

.form_add__content .field.first {
  max-width: 24rem;
}

.field-checked {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.field-checked input {
  margin-right: 0.625rem;
}

.field-checked label {
  font-weight: var(--font-w-sm);
  color: var(--slate-two);
}

.field-multi {
  cursor: pointer;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.field-multi input {
  display: none;
}

.field-multi input:checked ~ .check::before {
  left: 0.5rem;
}

.field-multi .check {
  position: relative;
  margin-right: 0.5rem;
  width: 1.625rem;
  height: 1rem;
  background-color: #e3e8ee;
  border-radius: 0.5rem;
  border: 0.0625rem solid #d8dbe1;
}

.field-multi .check::before {
  content: "";
  position: absolute;
  top: -0.0625rem;
  left: 0;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: white;
  border: 0.0625rem solid #b8bcc3;
  transition: all 300ms ease;
}

.field-multi .text {
  color: var(--slate-two);
}
