.table {

	&__header {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;

		margin-bottom: rem(10);
	}

	&__nav {
		margin-bottom: rem(26);

		ul {
			position: relative;
			display: flex;
			flex-wrap: wrap;

			&::before {
				content: "";
				position: absolute;
				left: 0;
				bottom: rem(-8);
				width: 100%;
				height: rem(1);
				background-color: var(--border-color);
				z-index: -1;
			}

			li {
				margin-right: rem(24);

				&:last-child {
					margin-right: 0;
				}

				a {
					font-weight: var(--font-w-sm);
					color: var(--battleship-grey);

					padding-bottom: rem(7);

					border-bottom: rem(2) solid transparent;

					&:hover {
						color: var(--cornflower);

						border-color: var(--cornflower);
					}
				}
			}

			li.active {

				a {
					color: var(--cornflower);
					border-color: var(--cornflower);
				}

			}
		}
	}

	&__rows {
		display: flex;
		flex-direction: column;

		.row-first-ngutter {
			.col {
				&:first-of-type {
					padding-left: 0;
					padding-right: 0;
				}
			}
		}

		.row-last-ngutter {
			.col {
				&:last-of-type {
					padding-left: 0;
					padding-right: 0;
				}
			}
		}

		.row {
			position: relative;
			padding: rem(12) 0;

			display: flex;
			flex-wrap: wrap;

			border-bottom: rem(1) solid var(--border-color);

			&:hover {
				background-color: #f7fafc;

				.other_list {
					opacity: 1;
				}
			}

			.other_list {
				position: absolute;
				top: rem(11);
				right: rem(8);

				height: rem(24);

				opacity: 0;
			}

			.link {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
			}

			input {
				position: relative;
				z-index: 2;
			}

			.col {
				position: relative;
				display: flex;
				flex-wrap: wrap;
				align-items: center;

				padding-left: rem(12);
				padding-right: rem(12);

				.ic-reload {
					position: absolute;
					top: rem(2);
					right: rem(4);
				}

				b, span {
					white-space: nowrap;
				}

				b {
					font-weight: var(--font-w-sm);
				}

				span {
					color: #3e4559;
					letter-spacing: 0.5px;
				}
			}
			
			.col.w-1 {
				width: 1%;
			}
			.col.w-2 {
				width: 2%;
			}
			.col.w-3 {
				width: 3%;
			}
			.col.w-5 {
				width: 5%;
			}
			.col.w-6 {
				width: 6%;
			}
			.col.w-7 {
				width: 7%;
			}
			.col.w-8 {
				width: 8%;
			}
			.col.w-9 {
				width: 9%;
			}
			.col.w-10 {
				width: 10%;
			}
			.col.w-11 {
				width: 11%;
			}
			.col.w-12 {
				width: 12%;
			}
			.col.w-13 {
				width: 13%;
			}
			.col.w-14 {
				width: 14%;
			}
			.col.w-15 {
				width: 15%;
			}
			.col.w-16 {
				width: 16%;
			}
			.col.w-17 {
				width: 17%;
			}
			.col.w-18 {
				width: 18%;
			}
			.col.w-20 {
				width: 20%;
			}
			.col.w-22 {
				width: 22%;
			}
			.col.w-25 {
				width: 25%;
			}
			.col.w-27 {
				width: 27%;
			}
			.col.w-30 {
				width: 30%;
			}
			.col.w-32 {
				width: 32%;
			}
			.col.w-35 {
				width: 35%;
			}
			.col.w-40 {
				width: 40%;

			}
			.col.w-41 {
				width: 41%;
			}
			.col.w-42 {
				width: 42%;
			}
			.col.w-43 {
				width: 43%;
			}
			.col.w-44 {
				width: 44%;
			}
			.col.w-45 {
				width: 45%;
			}
			.col.w-16 {
				width: 16%;
			}

			.c-light {
				color: #6c7687;
				letter-spacing: 0.5px;
			}

			@include db_media {
				.md-w-10 {
					width: 10% !important;
				}
				.md-w-19 {
					width: 19% !important;
				}
				.md-w-20 {
					width: 20% !important;
				}
				.md-w-24 {
					width: 24% !important;
				}
				.md-w-25 {
					width: 25% !important;
				}
				.md-w-26 {
					width: 26% !important;
				}
				.md-w-30 {
					width: 30% !important;
				}
			}
			
		}

		.row.first {
			b {
				font-size: rem(12);
				letter-spacing: 0.5px;
			}
		}
	}

	&__result {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		margin-top: rem(20);
		margin-left: rem(32);
		margin-right: rem(16);

		.first {
			b, span {
				color: #3e4559;
			}

			// b {
			// 	margin-right: rem(2);
			// }
		}
	}

}

.table.table-taxes {


	@include db_media {
		.col {
			b, span {
				font-size: rem(12);
				white-space: normal;
			}
		}
	}

}